
@-webkit-keyframes zoomInX {
from {
    opacity: 0;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
}
50% {
    opacity: 1;
}
}
@keyframes zoomInX {
from {
    opacity: 0;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
}
50% {
    opacity: 1;
}
}
.zoomInX {
  -webkit-animation-name: zoomInX;
          animation-name: zoomInX;
}
@-webkit-keyframes zoomOutX {
from {
    opacity: 1;
}
50% {
    opacity: 0;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
}
to {
    opacity: 0;
}
}
@keyframes zoomOutX {
from {
    opacity: 1;
}
50% {
    opacity: 0;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
}
to {
    opacity: 0;
}
}
.zoomOutX {
  -webkit-animation-name: zoomOutX;
          animation-name: zoomOutX;
}



