
@-webkit-keyframes shake {
from {
    -webkit-transform: rotate(-4deg);
            transform: rotate(-4deg);
}
,
to {
  -webkit-transform: rotate(4deg);
          transform: rotate(4deg);
}
}
@keyframes shake {
from {
    -webkit-transform: rotate(-4deg);
            transform: rotate(-4deg);
}
,
to {
  -webkit-transform: rotate(4deg);
          transform: rotate(4deg);
}
}
.icon {
  position: relative;
  background-color: transparent;
  margin: 14px;
  height: 52px;
  width: 52px;
  border-radius: 10px;
  -webkit-box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.07);
          box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.07);
  color: #777;
  font-weight: 900;
  font-size: 12px;
  line-height: 52px;
  text-align: center;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  cursor: pointer;
}
.icon .icon-delete-btn {
  display: block;
  position: absolute;
  width: 8px;
  height: 8px;
  border-radius: 7px;
  right: 6px;
  top: 6px;
  border: 1px solid rgba(255, 255, 255, 0.4);
  background: rgba(255, 255, 255, 0.2);
}



