
@-webkit-keyframes zoomInY {
from {
    opacity: 0;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
}
50% {
    opacity: 1;
    tranform: scaleY(1)
}
}
@keyframes zoomInY {
from {
    opacity: 0;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
}
50% {
    opacity: 1;
    tranform: scaleY(1)
}
}
.zoomInY {
  -webkit-animation-name: zoomInY;
          animation-name: zoomInY;
}
@-webkit-keyframes zoomOutY {
from {
    opacity: 1;
}
50% {
    opacity: 0;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
}
to {
    opacity: 0;
}
}
@keyframes zoomOutY {
from {
    opacity: 1;
}
50% {
    opacity: 0;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
}
to {
    opacity: 0;
}
}
.zoomOutY {
  -webkit-animation-name: zoomOutY;
          animation-name: zoomOutY;
}



